import moment from 'moment'

import * as types from './mutations-types'
import services from '../services'

export const setUserLogged = (context, userLogged) => {
  services.setUserLogged(userLogged)
  context.commit(types.SET_USER_LOGGED, userLogged)
}

export const setUserToken = (context, userToken) => {
  services.setUserToken(userToken)
  context.commit(types.SET_USER_TOKEN, userToken)
}

export const startRefreshInterval = context => {
  context.dispatch('fetchAllData')
  if (!context.getters.refreshIntervalRunning) {
    setInterval(() => {
      context.commit(types.START_REFRESH_INTERVAL)
      context.dispatch('fetchAllData')
    }, 1000 * 60)
  }
}

export const fetchAllData = context => {
  context.dispatch('fetchHappeningChatsData')
  context.dispatch('fetchFinishedChatsData')
  context.dispatch('fetchChatAgents')
  context.dispatch('fetchGroups')
}

export const fetchGroups = async context => {
  const groups = await services.getGroups()
  context.commit(types.SET_GROUPS, groups)
}

export const fetchHappeningChatsData = async context => {
  let hapeningFilter = {
    filters: [
      {
        property: 'period',
        operator: 3,
        value: moment()
          .startOf('day')
          .format()
      },
      {
        property: 'period',
        operator: 1,
        value: moment()
          .endOf('day')
          .format()
      }
    ]
  }
  if (context.getters.selectedHappeningFilter) {
    hapeningFilter = {
      ...context.getters.selectedHappeningFilter
    }
  }
  if (context.getters.selectedGroupFilter) {
    hapeningFilter.filters.push({
      property: 'group.id',
      operator: 2,
      value: context.getters.selectedGroupFilter.id
    })
  }
  const happeningChatsData = await services.getHappeningChatsData(
    hapeningFilter
  )
  context.commit(types.SET_HAPPENING_CHATS_DATA, happeningChatsData)
}

export const fetchFinishedChatsData = async context => {
  let finishedFilter = {
    filters: [
      {
        property: 'period',
        operator: 3,
        value: moment()
          .startOf('day')
          .format()
      },
      {
        property: 'period',
        operator: 1,
        value: moment()
          .endOf('day')
          .format()
      }
    ]
  }
  if (context.getters.selectedFinishedFilter) {
    finishedFilter = {
      ...context.getters.selectedFinishedFilter
    }
  }
  if (context.getters.selectedGroupFilter) {
    finishedFilter.filters.push({
      property: 'group.id',
      operator: 2,
      value: context.getters.selectedGroupFilter.id
    })
  }
  const finishedChatsData = await services.getFinishedChatsData(finishedFilter)
  context.commit(types.SET_FINISHED_CHATS_DATA, finishedChatsData)
}

export const fetchChatAgents = async context => {
  if (
    context.getters.selectedGroupFilter &&
    context.getters.selectedGroupFilter.id !== 'all'
  ) {
    context.commit(
      types.SET_CHAT_AGENTS,
      context.getters.selectedGroupFilter.agents
    )
  } else {
    const chatAgents = await services.getChatAgents()
    context.commit(types.SET_CHAT_AGENTS, chatAgents)
  }
}

export const selectGroupFilter = (context, group) => {
  if (group.id === 'all') {
    context.commit(types.SELECT_GROUP_FILTER, undefined)
  } else {
    context.commit(types.SELECT_GROUP_FILTER, group)
  }

  context.dispatch('fetchAllData')
}

export const selectFinishedFilter = (context, filter) => {
  context.commit(types.SELECT_FINISHED_FILTER, filter)
  context.dispatch('fetchFinishedChatsData')
}

export const selectHappeningFilter = (context, filter) => {
  context.commit(types.SELECT_HAPENNING_FILTER, filter)
  context.dispatch('fetchHappeningChatsData')
}

export const setAgentOffline = async (context, id) => {
  const agent = await services.setAgentOffline(id)
  context.commit(types.SET_AGENT_OFFLINE, agent)
  return agent
}

export const setAllAgentsOffline = async context => {
  await services.setAllAgentsOffline()
  context.commit(types.SET_ALL_AGENTS_OFFLINE)
}
