import store from '@/store'

const eventMethod = window.addEventListener ? 'addEventListener' : 'attachEvent'
const eventer = window[eventMethod]
const messageEvent = eventMethod === 'attachEvent' ? 'onmessage' : 'message'
const appId = 'adfe05df-0e04-43ec-b196-fb4e07b72f6b'

eventer(
  messageEvent,
  function OnMessage(event) {
    if (event.data.userLogged) {
      store.dispatch('setUserLogged', event.data.userLogged)
    }

    if (event.data.setUserToken) {
      store.dispatch('setUserToken', event.data.setUserToken)
    }

    if (event.data.appUrl) {
      store.dispatch('changeUrl', event.data.appUrl)
    }
  },
  false
)

const post = (event, payload) => {
  if (window.parent) {
    window.parent.postMessage(
      Object.assign({ name: event, octaapp: true, id: appId }, payload || {}),
      '*'
    )
  }
}

export const connect = () => post('appConnected')

export const postMessage = post
