<template>
  <div class="container">
    <v-select 
      :placeholder="'Todos os grupos'"
      :options="groupsOptions"
      :value="handleSelectedGroupFilter"
      class="filter-group-input"
      label="name" 
      @input="selectGroupFilter"/>
    <div class="panels-container">
      <div class="row">
        <div class="col-sm-12 col-md-6">
          <div class="row">
            <div class="col-sm-12">
              <HappeningChatsPanel/>
            </div>
            <div class="col-sm-12">
              <FinishedChatsPanel/>
            </div>
          </div>
        </div>
        <div class="col-sm-12 col-md-6">
          <div class="row">
            <div class="col-sm-12">
              <AgentsStatusPanel/>
            </div>
            <!-- <div class="col-sm-12">
              <SatisfationPanel/>
            </div> -->
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import vSelect from 'vue-select'

import HappeningChatsPanel from './HappeningChatsPanel'
import FinishedChatsPanel from './FinishedChatsPanel'
import AgentsStatusPanel from './AgentsStatusPanel'
import SatisfationPanel from './SatisfactionPanel'

export default {
  components: {
    HappeningChatsPanel,
    FinishedChatsPanel,
    AgentsStatusPanel,
    SatisfationPanel,
    vSelect
  },
  data() {
    return {
      allGroups: {
        id: 'all',
        name: 'Todos os grupos'
      }
    }
  },
  computed: {
    ...mapGetters(['groups', 'selectedGroupFilter']),
    groupsOptions() {
      return [this.allGroups, ...this.groups]
    },
    handleSelectedGroupFilter() {
      return this.selectedGroupFilter || ''
    }
  },
  methods: {
    ...mapActions(['selectGroupFilter'])
  }
}
</script>

<style>
.panel-top-right-cta {
  position: absolute !important;
  top: 10px;
  right: 15px;
}

.panel-item {
  position: relative;
  width: auto;
  padding: 15px;
  border: 1px solid #eee;
  border-radius: 4px;
  margin-bottom: 15px;
}

.panel-title {
  font-size: 2.5em;
  font-weight: 300;
  color: #aaa;
  margin: 0;
}

.panel-inner-card {
  padding: 15px;
  margin-bottom: 20px;
  background: #eee;
  text-align: center;
  line-height: 1;
}

.panel-inner-value {
  font-size: 4em;
  color: #3c8dc6;
  display: inline;
}

.panel-inner-card p {
  margin: 0 !important;
}

.text-primary {
  color: #5da9dd !important;
}

.text-success {
  color: #50b848 !important;
}

.text-warning {
  color: #ecb241 !important;
}

.text-default {
  color: #666 !important;
}

.text-danger {
  color: #d7443d !important;
}

.vertical-middle-align {
  vertical-align: middle !important;
}

/*small devices*/
@media screen and (max-width: 768px) {
}
</style>

<style>
.filter-group-input {
  width: 270px !important;
}

.panels-container {
  margin: 15px 0;
}

/*small devices*/
@media screen and (max-width: 768px) {
  .mobile-w100 {
    width: 100% !important;
  }

  .filter-group-input {
    width: 100% !important;
  }

  .panel-title {
    font-size: 1.5em;
    tex-align: center;
  }

  .panel-title i {
    font-size: 2em;
  }

  .panel-top-right-cta {
    position: relative !important;
    display: block;
    top: 0px;
    right: 0px;
    margin-top: 8px;
    margin-bottom: 8px;
    width: 100% !important;
  }
}
</style>
