<template>
  <div class="panel-item">
    <p class="panel-title">
    <i class="icon icon-agent vertical-middle-align" /> Agentes</p>
    <button 
      v-if="onlineCount > 0 || busyCount > 0" 
      type="button" 
      class="btn btn-outline-primary btn-outline-primary-gray-border space-top-md panel-top-right-cta" 
      @click="setAllAgentsOffline"> desconectar todos</button>
    <div class="row">
      <div class="col-sm-12 col-md-4">
        <div class="panel-inner-card">
          <p class="panel-inner-value text-success">{{ onlineCount }} </p>
          <p>agentes online</p>
        </div>
      </div>
      <div class="col-sm-12 col-md-4">
        <div class="panel-inner-card">
          <p class="panel-inner-value text-warning"> {{ busyCount }}</p>
          <p>agentes ocupados</p>
        </div>
      </div>
      <div class="col-sm-12 col-md-4">
        <div class="panel-inner-card">
          <p class="panel-inner-value text-default"> {{ offlineCount }}</p>
          <p>agentes offline</p>
        </div>
      </div>
    </div>
    <div class="panel-agents">
      <div class="panel-agents-heading">
        <span class="panel-agents-name-item">Agentes</span>
        <span class="panel-agents-status-item">Status</span>
      </div>
      <ul 
        v-for="agent in filterAgents" 
        :key="agent.id" 
        class="panel-agents-list">
        <li class="panel-agents-list-item">
          <span class="panel-agents-name-item">
            <span 
              :class="{ 'agent-initials-align-center' : !agent.thumbUrl }" 
              class="circle-agent">
              <img 
                v-if="agent.thumbUrl" 
                :src="agent.thumbUrl" 
                style="width: 100%; height: 100%;" 
                class="vertical-middle-align">
              <span 
                v-else 
                class="vertical-middle-align">{{ handleNameInitials(agent.name) }}</span>
            </span> {{ agent.name }}
          </span>
          <span class="panel-agents-status-item">
            <span 
              :class="`status-${parseConnectionStatus(agent.connectionStatus)}`" 
              class="circle-status" /> {{ parseConnectionStatus(agent.connectionStatus) }}
            <button
              v-tooltip.left="'Desconectar'"
              v-if="agent.connectionStatus !== 1" 
              type="button" 
              href="#" 
              class="icon icon-out logout-agent-btn logout-agent-btn-hover-show text-primary btn-sem-style" 
              @click="setAgentOffline(agent.id)"/>
          </span>
        </li>
      </ul>
    </div>

    <button 
      v-if="hasMore"
      type="button" 
      class="btn btn-outline-primary btn-outline-primary-gray-border space-top-md mobile-w100" 
      @click="loadMore()"> carregar mais</button>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
export default {
  data() {
    return {
      filterAgents: []
    }
  },
  computed: {
    ...mapGetters(['chatAgents', 'selectedGroupFilter']),
    agentsModel() {
      if (this.selectedGroupFilter && this.selectedGroupFilter.id !== 'all') {
        return this.selectedGroupFilter.agents
      } else {
        return this.chatAgents
      }
    },
    onlineCount() {
      return this.agentsModel.filter(a => a.connectionStatus === 0).length
    },
    offlineCount() {
      return this.agentsModel.filter(a => a.connectionStatus === 1).length
    },
    busyCount() {
      return this.agentsModel.filter(a => a.connectionStatus === 2).length
    },
    hasMore() {
      return (
        this.agentsModel.length > 3 &&
        this.filterAgents.length !== this.agentsModel.length
      )
    }
  },
  watch: {
    agentsModel: function() {
      if (this.filterAgents.length <= 3) {
        this.filterAgents = this.agentsModel.filter((a, i) => i <= 2)
      } else {
        this.filterAgents = [...this.agentsModel]
      }
    }
  },
  methods: {
    ...mapActions(['setAllAgentsOffline', 'setAgentOffline']),
    handleNameInitials(name) {
      return name.split('')[0]
    },
    parseConnectionStatus(status) {
      let parsedStatus
      switch (status) {
        case 0:
          parsedStatus = 'online'
          break
        case 1:
          parsedStatus = 'offline'
          break
        case 2:
          parsedStatus = 'ocupado'
          break
        default:
          parsedStatus = 'offline'
      }
      return parsedStatus
    },
    loadMore() {
      this.filterAgents = [...this.agentsModel]
    }
  }
}
</script>

<style>
.panel-agents {
  max-height: 230px;
  overflow-y: auto;
  overflow-x: hidden;
}

.panel-agents-list {
  list-style-type: none;
  padding: 0;
  margin: 0 !important;
}

.panel-agents-list-item {
  padding: 4px 2px;
  border-top: 1px solid transparent;
  border-bottom: 1px solid transparent;
}

.panel-agents-list-item:hover {
  background: #eef;
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
}
.panel-agents-name-item {
  display: inline-block;
  text-overflow: ellipsis;
  width: 70%;
  overflow: hidden;
  white-space: nowrap;
  max-width: 70%;
  vertical-align: middle;
}

.panel-agents-status-item {
  display: inline-block;
  width: 30%;
}

.panel-agents-heading {
  font-weight: 700;
}

.logout-agent-btn {
  float: right;
  font-size: 1rem;
  text-decoration: none !important;
}

.logout-agent-btn-hover-show {
  display: none;
}

.panel-agents-list-item:hover:hover .logout-agent-btn-hover-show {
  display: block;
}

.circle-status {
  display: inline-block;
  width: 6px;
  height: 6px;
  border-radius: 100%;
}

.circle-agent {
  display: inline-block;
  width: 1.6rem;
  height: 1.6rem;
  border-radius: 100%;
  background: #ddd;
  vertical-align: middle;
  text-align: center;
  margin-left: 8px;
  margin-right: 8px;
  color: #fff;
  font-size: 0.8rem;
  font-weight: 500;
  overflow: hidden;
}

.agent-initials-align-center {
  padding-top: 0.2rem;
}

.status-online {
  background: #50b848;
}

.status-ocupado {
  background: #ecb241;
}

.status-offline {
  background: #666;
}

.btn-sem-style {
  border: none !important;
  padding: 0 !important;
  box-shadow: none !important;
  background: transparent;
}
</style>
