import * as types from './mutations-types'

export default {
  [types.SET_USER_LOGGED](state, userLogged) {
    state.userLogged = userLogged
  },
  [types.SET_USER_TOKEN](state, userToken) {
    state.userToken = userToken
  },
  [types.START_REFRESH_INTERVAL](state) {
    state.refreshIntervalRunning = true
  },
  [types.SET_CHAT_AGENTS](state, agents) {
    state.chatAgents = agents
  },
  [types.SET_GROUPS](state, groups) {
    state.groups = groups
  },
  [types.SELECT_GROUP_FILTER](state, group) {
    state.selectedGroupFilter = group
  },
  [types.SELECT_FINISHED_FILTER](state, filter) {
    state.selectedFinishedFilter = filter
  },
  [types.SELECT_HAPENNING_FILTER](state, filter) {
    state.selectedHappeningFilter = filter
  },
  [types.SET_HAPPENING_CHATS_DATA](state, data) {
    state.happeningChatsData = data
  },
  [types.SET_FINISHED_CHATS_DATA](state, data) {
    state.finishedChatsData = data
  },
  [types.SET_GROUPED_DATA](state, data) {
    state.happeningChatsData = data.happening
    state.finishedChatsData = data.finalized
  },
  [types.SET_AGENT_OFFLINE](state, agent) {
    state.chatAgents.find((a, i) => {
      if (a.id === agent.id) {
        state.chatAgents[i].connectionStatus = agent.connectionStatus
      }
    })
  },
  [types.SET_ALL_AGENTS_OFFLINE](state) {
    state.chatAgents = state.chatAgents.map(a => ({
      ...a,
      connectionStatus: 1
    }))
  }
}
