<template>
  <div class="panel-item">
    <p class="panel-title">
    <i class="icon icon-realizadas vertical-middle-align" /> Atendimento</p>
    <v-select
      :placeholder="'Hoje'"
      :options="dateFilter"
      class="filter-group-input panel-top-right-cta"
      label="name"
      @input="handleFetch"/>
    <div class="row">
      <div class="col-sm-12 col-md-6">
        <div class="panel-inner-card">
          <p
            v-if="finishedChatsData.closed"
            class="panel-inner-value">{{ finishedChatsData.closed.total }}</p>
          <p>conversas realizadas</p>
        </div>
      </div>
      <div class="col-sm-12 col-md-6">
        <div class="panel-inner-card">
          <p
            v-if="finishedChatsData.missed"
            class="panel-inner-value text-danger">{{ finishedChatsData.missed.total }}</p>
          <p>conversas perdidas</p>
        </div>
      </div>
      <div class="col-sm-12 col-md-6">
        <div class="panel-inner-card">
          <p
            v-if="finishedChatsData.closed && finishedChatsData.closed.duration"
            class="panel-inner-value">{{ finishedChatsData.closed.duration.average | durationTime }}</p>
          <p>tempo mdio de durao</p>
        </div>
      </div>
      <div class="col-sm-12 col-md-6">
        <div class="panel-inner-card">
          <p
            v-if="finishedChatsData.missed && finishedChatsData.missed.onHold"
            class="panel-inner-value">{{ (finishedChatsData.closed.onHold.average + finishedChatsData.missed.onHold.average) | durationTime }}</p>
          <p>tempo mdio de espera</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import vSelect from 'vue-select'

export default {
  components: { vSelect },
  computed: {
    ...mapGetters(['finishedChatsData']),
    dateFilter() {
      return [
        {
          name: 'Hoje',
          value: {
            filters: [
              {
                property: 'period',
                operator: 3,
                value: this.$moment()
                  .startOf('day')
                  .format()
              },
              {
                property: 'period',
                operator: 1,
                value: this.$moment()
                  .endOf('day')
                  .format()
              }
            ]
          }
        },
        {
          name: 'ltima hora',
          value: {
            filters: [
              {
                property: 'period',
                operator: 3,
                value: this.$moment()
                  .startOf('hour')
                  .format()
              },
              {
                property: 'period',
                operator: 1,
                value: this.$moment()
                  .endOf('hour')
                  .format()
              }
            ]
          }
        }
      ]
    }
  },
  methods: {
    ...mapActions(['selectFinishedFilter']),
    handleFetch(filter) {
      if (filter) {
        this.selectFinishedFilter(filter.value)
      }
    }
  }
}
</script>

