import http from '@/http'
import { getParameterHeaders } from './headers'

export const getChatAgents = () => {
  return http
    .get('agents/status', {
      headers: getParameterHeaders()
    })
    .then(res => res.data)
}

export const setAgentOffline = id => {
  const status = 1
  return http
    .put(`agents/${id}/status`, { status }, { headers: getParameterHeaders() })
    .then(res => res.data)
}

export const setAllAgentsOffline = () => {
  const status = 1
  return http
    .put(`agents/status`, { status }, { headers: getParameterHeaders() })
    .then(res => res.data)
}
