<template>
  <div 
    id="app" 
    class="container-chat">
    <Dashboard-container v-if="happeningChatsData && groups && chatAgents && finishedChatsData" />
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import DashboardContainer from './components/DashboardContainer'
export default {
  name: 'App',
  components: {
    DashboardContainer
  },
  props: {
    inIframe: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    ...mapGetters([
      'userToken',
      'happeningChatsData',
      'groups',
      'chatAgents',
      'finishedChatsData'
    ])
  },
  created() {
    if (!this.inIframe && !this.userToken) {
      window.location = '/login?returnUrl=chat/dashboard'
    }
    this.fetchGroups()
    this.startRefreshInterval()
  },
  methods: {
    ...mapActions(['startRefreshInterval', 'fetchGroups'])
  }
}
</script>

<style>
* {
  box-sizing: border-box;
}

html,
body,
#app {
  height: 100%;
}

.container-chat {
  background: #fff;
}

body {
  margin: 0;
  font-family: Arial, Helvetica Neue, Helvetica, sans-serif;
  font-size: 12px;
  line-height: 1.5;
  color: #666;
}
</style>
