import * as session from '@/services/session'

export const userToken = ({ userToken }) => userToken || session.getUserToken()
export const refreshIntervalRunning = ({ refreshIntervalRunning }) =>
  refreshIntervalRunning

export const dataReady = ({
  chatAgents,
  groups,
  finishedChatsData,
  happeningChatsData
}) =>
  chatAgents && groups && finishedChatsData && happeningChatsData ? true : false

export const chatAgents = ({ chatAgents }) => chatAgents

export const groups = ({ groups }) => groups

export const selectedGroupFilter = ({ selectedGroupFilter }) =>
  selectedGroupFilter

export const selectedHappeningFilter = ({ selectedHappeningFilter }) =>
  selectedHappeningFilter

export const selectedFinishedFilter = ({ selectedFinishedFilter }) =>
  selectedFinishedFilter

export const finishedChatsData = ({ finishedChatsData }) => finishedChatsData

export const happeningChatsData = ({ happeningChatsData }) => happeningChatsData
