import http from '@/http'
import { getParameterHeaders } from './headers'

export const getHappeningChatsData = filter => {
  return http
    .post('/dashboard/rooms/happening', filter, {
      headers: getParameterHeaders()
    })
    .then(res => res.data)
}

export const getFinishedChatsData = filter => {
  return http
    .post('/dashboard/rooms/finalized', filter, {
      headers: getParameterHeaders()
    })
    .then(res => res.data)
}

export const getGroupedData = filter => {
  return http
    .post('/dashboard/rooms/grouped', filter, {
      headers: getParameterHeaders()
    })
    .then(res => res.data)
}
